// UncontrolledLottie.jsx
import React, { Component } from 'react';
import Lottie from 'react-lottie';
import animationData from './Solicitar.json';

class UncontrolledLottie extends Component {


  render(){

    const defaultOptions = {
      loop: true,
      autoplay: true,
      animationData: animationData,
      rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice'
      }
    };

    return(
        <div>
        <h1 style={{color: "#0e34a0"}} >1- SOLICITAÇÃO</h1>
        <Lottie options={defaultOptions}
          style={{flex:1, justifyContent:'center', alignItems: 'center',}}
          width={'auto'}
          height={200}
        />
        <br />
         <h3 style={{color: "#000"}}>
        Você faz uma solicitação no nosso site entramos em contato e te mostramos sua proposta personalizada, 7
        com relatórios do seu score.</h3 >

        <br />
        </div>

    )
  }
}

export default UncontrolledLottie;
