import React from 'react';
import { useDispatch } from 'react-redux';
import InputMask from 'react-input-mask';
import { Form, Input } from '@rocketseat/unform';
import * as Yup from 'yup';
import FormsA from '../../components/Lottie/FormsA';

import { signUpRequest } from '../../store/modules/auth/actions';

const schema = Yup.object().shape({
  name: Yup.string().required('Nome é obrigatório'),
  email: Yup.string()
    .email('Insira um e-mail válido')
    .required('E-mail é obrigatório'),
  phone: Yup.string()
    .min(11, 'No mínimo 11 caracteres')
    .required('Telefone é obrigatório'),
  cpf: Yup.string()
    .min(11, 'No mínimo 11 caracteres')
    .required('CPF é obrigatório'),
});

export default function Signup() {


  const dispatch = useDispatch();


  function handleSubmit(data) {
    dispatch(signUpRequest(data))
  

}


  return (
    <>
      <FormsA />
      
      <Form  schema={schema} onSubmit={handleSubmit}>
        <Input name="name" type="name"  placeholder="NOME COMPLETO" />
        <Input name="email" type="email" placeholder="SEU  E-MAIL" />

        <InputMask mask={'(99)-99999-9999'}  type="phone">
            {() => <Input name="phone" type="phone" placeholder="TELEFONE WHATSAPP" />}
        </InputMask>
        
        <InputMask name="cpf" mask={'999.999.999-99'} type="cpf" placeholder="000.000.000-00">
          {() => <Input name="cpf" type="cpf" placeholder="CPF" />}   
        </InputMask> 



        <button type="submit" className="btn btn">CADASTRAR-SE</button>
  
      </Form>
    </>
  );
}
