// UncontrolledLottie.jsx
import React, { Component } from 'react';
import Lottie from 'react-lottie';
import animationData from './rocket.json';



import score from '../../assets/img/como-ficou-score.png';
import como from '../../assets/img/como-aumentar-o-score.png';

class UncontrolledLottie extends Component {


  render(){

    const defaultOptions = {
      loop: true,
      autoplay: true,
      animationData: animationData,
      rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice'
      }
    };

    return(
      <>
        <div>
        <h4 style={{color:"#000099"}} >COMO SABER QUAL O SCORE IDEAL PARA FINANCIAR UM CARRO?</h4>
        <p style={{color:"#000"}} >Um bom score é aquele que está acima de 700 pontos – mas isso nem sempre é uma regra. Entenda os riscos de inadimplência comparados com cada nível de pontuação:</p>
        <li style={{color:"#000"}} >Score até 300 pontos: risco alto de inadimplência.</li>
        <li style={{color:"#000"}} >Score entre 300 e 700 pontos – risco médio de inadimplência.</li>
        <li style={{color:"#000"}} >Score acima de 700 pontos – risco baixo de inadimplência.</li>
        <img className="img-fluid"
         src={como} 
         width={300}
         height={180}
         
         style={{margin:"0 auto",display: "block"}}
         />
         <h4 style={{color:"#000099"}} >PARA AUMENTAR O SCORE, PLANEJAMENTO FINANCEIRO</h4>
        <img className="img-fluid"
         src={score} 
         width={300}
         height={320}
         style={{margin:"0 auto",display: "block"}}
         />

        <h4 style={{color:"#000099"}} > PARA AUMENTAR O SCORE, LIMPE O SEU NOME </h4>

      
        
        <Lottie options={defaultOptions}
          style={{flex:1, justifyContent:'center', alignItems: 'center',}}
          width={200}
          height={200}
        />
        <h4 style={{color: '#000099'}}>PARA AUMENTAR O SCORE, FAÇA O CADASTRO POSITIVO</h4>
       
          <h3 style={{color: "#000"}}>
          Para limpar o nome é fundamental.
          Se você tem dívidas atrasadas,
          negocie com os credores e pague todas elas.
          Essa deve ser a sua prioridade.
          Sem isso,
          dificilmente você vai conseguir mudar o jeito que o mercado analisa o seu cadastro.
        </h3>

        <h4 style={{color:"#000099"}} >PARA AUMENTAR O SCORE, EVITE CONSULTAS DESNECESSÁRIAS AO SEU CPF</h4>
        < br />

        </div>
      </>
      )
  }
}

export default UncontrolledLottie;
